import React, { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeRaw from "rehype-raw"; // HTML    

interface MarkdownRendererProps {
  filePath: string; // Markdown  
}

const MarkdownRenderer: React.FC<MarkdownRendererProps> = ({ filePath }) => {
  const [content, setContent] = useState<string>("");

  useEffect(() => {
    fetch(filePath)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`Failed to load Markdown file: ${filePath}`);
        }
        return response.text();
      })
      .then((text) => setContent(text))
      .catch((error) => console.error(error));
  }, [filePath]);

  // YouTube    iframe  
  const renderYouTubeEmbed = (url: string) => {
    const match = url.match(
      /(?:https?:\/\/)?(?:www\.)?(?:youtube\.com\/watch\?v=|youtu\.be\/)([\w-]+)/
    );
    if (match) {
      return (
        <div className="block w-full">
          {/*  `aspect-video`     `block`  */}
          <div className="relative aspect-[16/9]">
            <iframe
              className="absolute top-0 left-0 w-full h-full"
              src={`https://www.youtube.com/embed/${match[1]}`}
              allowFullScreen
            />
          </div>
        </div>
      );
    }
    return (
      <a href={url} className="text-blue-500 underline">
        {url}
      </a>
    );
  };

  if (!content) {
    return <p>Loading...</p>;
  }

  return (
    <div className="w-full max-w-[900px] mx-auto prose">
      <ReactMarkdown
        remarkPlugins={[remarkGfm]} // GFM 
        rehypePlugins={[rehypeRaw]} // HTML   
        components={{
          a: ({ node, href, children, ...props }) => {
            if (href) return renderYouTubeEmbed(href);
            return (
              <a href={href} {...props}>
                {children}
              </a>
            );
          },
          img: ({ node, ...props }) => (
            <div className="flex justify-center w-full my-4">
              <img {...props} className="max-w-full h-auto min-h-[300px]" />
            </div>
          ),
        }}
      >
        {content}
      </ReactMarkdown>
    </div>
  );
};

export default MarkdownRenderer;
